
import XHeader from 'vux/src/components/x-header/index.vue'

import {santaWish} from '../config/constant'
import Wechat from '../utils/wechat.js'
import {santaPicture as santa, activityOssPrefix } from '../config/index'
import santaApi from '../store/apis/santaWish'
import {mapState} from 'vuex'
import { local } from './../utils/storage'
import imgLabel1 from './../assets/pigyear/img-label-1.png'
import imgLabel2 from './../assets/pigyear/img-label-2.png'
import imgLabel3 from './../assets/pigyear/img-label-3.png'
import imgTitle1 from './../assets/pigyear/img-title-1.png'
import imgTitle2 from './../assets/pigyear/img-title-2.png'
import imgTitle3 from './../assets/pigyear/img-title-3.png'
import imgToNext from './../assets/pigyear/img-to-next.png'
import imgToFinish from './../assets/pigyear/img-to-finish.png'
export default {
components: {
XHeader
},
data () {
return {
imgToNext,
imgToFinish,
number: santaWish.length,
index: '',
order: 0,
wish: santaWish,
santa: santa,
activityOssPrefix,
selectIndex: '',
hasShared: false,
hasSelected: false,
answer: '',
questions: [
{
imgLabel: imgLabel1,
imgTitle: imgTitle1,
options: [
'A. 一分没赚，软饭最好吃，啃老我最行',
'B. 维持温饱，苦难的人生，苍白的账户',
'C. 中产小资，只要没意外，每天订外卖',
'D. 地主土豪，懒得核算，总之花不完'
]
},
{
imgLabel: imgLabel2,
imgTitle: imgTitle2,
options: [
'A. 一分没花，蹭吃蹭喝蹭住',
'B. 月光 x 12，花哪了我也不知道',
'C. 年年有余，遏制欲望，美好人生',
'D. 花不完花不完，怎么努力也花不完'
]
},
{
imgLabel: imgLabel3,
imgTitle: imgTitle3,
options: [
'A. 吃遍百家饭，多躺一天是一天',
'B. 像现实低头，苟且一年又一年',
'C. 稳定求发展，提升自我展望未来',
'D. 生活太美好，海天盛筵走遍全球'
]
}
],
answers: []
}
},
created () {
this.init()
},
computed: {
...mapState({
myInfo: state => state.user.myInfo
})
},
methods: {
_getIndex () {
const letter = ['a', 'b', 'c', 'd']
let result = ''
this.answers.forEach(v => {
result += letter[v]
})
console.log(result)
return result
},
doStartGame () {
this.order ++
},
doToNext () {
if (this.answers[this.order - 1] == null) {
this.$vux.toast.show({ text: '请选择一个答案', type: 'text', position: 'center' })
} else {
if (this.order === this.questions.length) {
local.save(local.KEY_ACTIVITY_SELECTION, this._getIndex())
local.save(local.KEY_USER_VISITED, true)
this.$router.push({path: `/myWish/${this.myInfo.id}`, query: {}})
} else {
this.order ++
}
}
},
toResult () {
this.$router.push({
path: `/myWish/${this.myInfo.id}`,
query: { index: local.get(local.KEY_ACTIVITY_SELECTION) }
})
},
doSelectAnswer (index, optionIndex) {
if (this.answers[index] == null) {
this.answers.push(optionIndex)
} else {
this.answers[index] = optionIndex
this.$forceUpdate()
}
},
doSelectOption (index) {
if (!this.hasSelected) {
this.selectIndex = index
} else {
this.$vux.toast.show({ text: '您已选择', type: 'text' })
}
},
changeIndex () {
const i = Math.floor(Math.random() * this.number)
this.index = i
},
gotoWish () {
this.$router.push({path: `/myWish/${this.myInfo.id}`, query:{index: this.selectIndex}});
local.save(local.KEY_ACTIVITY_SELECTION, this.selectIndex)
},
async init () {
const activitySelection = local.get(local.KEY_ACTIVITY_SELECTION)
const activityShareStatus = local.get(local.KEY_ACTIVITY_SHARE_STATUS)
Wechat.initSantaShare(this.index, '  ')
if (activitySelection) {
this.hasSelected = true
}
if (activityShareStatus) {
this.hasShared = true
}
}
}
}
